@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
html {
    scroll-behavior: smooth;
}

body {
    background: #0a2146;
    font-family: 'Source Sans Pro', serif;
    margin: 0px;
    color: white;
    overflow-x: hidden;
}

body * {
    font-family: 'Source Sans Pro', serif;
}

.container {
    position: relative;
    margin-bottom: 150px;
}

.header-dot {
    display: inline-block;
    width: 8px;
    height: 8px;
    background: #6bea7c;
    margin-left: 2px;
}

p {
    margin: 0;
}

a {
    font-weight: 500;
    color: white;
    transition: color 0.25s;
}

a:hover {
    color: #6bea7c;
}

.animate__animated {
    -webkit-animation-fill-mode: forwards !important;
            animation-fill-mode: forwards !important;
}

#header, #home, #about, #experience, #education, #projects, #contact {
    opacity: 0;
}
.main-container {
    margin-left: 280px;
    padding-left: 150px;
    padding-right: 150px;
    transition: margin-left ease-out 350ms;
}

.fa-bars {
    display: none;
    font-size: 30px;
    position: fixed;
    left: 15px;
    top: 15px;
    cursor: pointer;
    z-index: 1;
}

@media only screen and (max-width: 1200px) {
    .main-container {
        padding-left: 50px;
        padding-right: 50px;
    }
}

@media only screen and (max-width: 1000px) {
    .fa-bars {
        display: block;
    }
    .main-container {
        margin-left: 0px;
    }
}

@media only screen and (max-width: 767px) {
    .fa-bars {
        display: block;
    }
    .main-container {
        padding-right: 25px;
        padding-left: 25px;
        overflow: hidden;
    }
}
header {
    position: fixed;
    height: 100vh;
    min-height: 100vh;
    width: 200px;
    border-right: 1px solid #666666;
    left: 0px;
    padding: 50px 40px;
    transition: left ease-out 350ms;
}

.fa-times {
    display: none;
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 30px;
    cursor: pointer;
}

.header-title {
    font-size: 50px;
    font-weight: bold;
    letter-spacing: 3px;
    cursor: pointer;
    margin: 0px;
}

nav {
    margin-top: 100px;
}

.nav-list {
    list-style: none;
    font-size: 20px;
    padding: 0;
    cursor: pointer;
    font-weight: bold;
}

.nav-item {
    padding: 8px 0px;
    transition: color ease 250ms;
}

.nav-item:hover {
    color: #6bea7c;
}

.nav-item.active {
    color: #6bea7c;
    border-bottom: 1px solid #6bea7c;
}

@media only screen and (max-width: 1000px) {
    header {
        left: -280px;
    }
    .fa-times {
        display: block;
    }
}
.home-container {
    height: 100vh;
    align-items: center;
}

.home-content {
    margin: 0 auto;
    text-align: center;
    position: relative;
    top: 60px;
}

.profile-image {
    height: 150px;
    width: 150px;
    border: 2px solid white;
    border-radius: 50%;
}

.profile-name {
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 50px;
    margin-bottom: 0px;
    margin-top: 10px;
}

.profile-title {
    font-weight: 200;
    margin-top: 0px;
    font-size: 35px;
    margin-bottom: 10px;
}

.social-links {
    list-style: none;
    margin-top: 50px;
    padding: 0px;
    margin-bottom: 0px;
}

.social-link {
    display: inline-block;
    font-size: 45px;
    margin: 0px 15px;
    cursor: pointer;
    transition: color ease 100ms, transform ease 100ms;
}

.social-link:hover {
    color: #6bea7c;
    transform: scale(1.2)
}

.hire-button {
    background: #FF4C60;
    color: white;
    display: inline-block;
    border-radius: 30px;
    font-size: 24px;
    font-weight: 600;
    padding: 15px 35px;
    border: none;
    outline: none;
    cursor: pointer;
    margin-top: 50px;
    box-shadow: 0px 5px 5px #000000;
}

.hire-button:hover {
    -webkit-animation: zoom-out-in 350ms forwards;
            animation: zoom-out-in 350ms forwards;
}

@-webkit-keyframes zoom-out-in {
    35% {
        transform: scale(0.8);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes zoom-out-in {
    35% {
        transform: scale(0.8);
    }
    100% {
        transform: scale(1);
    }
}

.scroll-tip {
    position: absolute;
    bottom: 25px;
    font-weight: 300;
    text-align: center;
    width: 100%;
}

.scroll-tip span {
    display: inline-block;
    margin-bottom: 10px;
    cursor: pointer;
}

.scroll-tip i {
    display: block;
    margin: -10px 0px;
    font-size: 20px;
    -webkit-animation: fadeInDownArrow 2s infinite;
            animation: fadeInDownArrow 2s infinite;
    color: #FF4C60;
}

.scroll-tip i:nth-of-type(2) {
    -webkit-animation-delay: 500ms;
            animation-delay: 500ms;
}

.scroll-tip i:nth-of-type(3) {
    -webkit-animation-delay: 1s;
            animation-delay: 1s;
}

@-webkit-keyframes fadeInDownArrow {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@keyframes fadeInDownArrow {
    0% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

@media only screen and (max-width: 767px) {
    .home-content {
        top: 40px;
    }
    .scroll-tip {
        display: none;
    }
    .profile-name {
        margin-top: 25px;
        font-size: 40px;
    }
    .social-links {
        margin-top: 30px;
    }
    .social-link {
        font-size: 40px;
    }
    .hire-button {
        margin-top: 30px;
    }
    .profile-title {
        font-size: 30px;
        margin-top: 0px;
    }
}
.about-header {
    font-size: 50px;
    text-align: center;
}

.about-header .header-dot {
    margin-left: 5px;
}

.bitmoji-waving {
    border: 2px solid white;
    height: 225px;
    width: 225px;
    border-radius: 50%;
    box-shadow: 5px 10px 25px #000000;
}

.about-text-container {
    margin-left: 50px;
    background-color: #FF4C60;
    border-radius: 20px;
    padding: 25px;
    box-shadow: 5px 10px 25px #000000;
    min-width: 250px;
}

.about-content {
    display: flex;
    align-items: center;
}

.about-text {
    font-weight: 300;
    font-size: 24px;
}

.tech-content {
    margin-top: 75px;
    margin-bottom: 75px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.tech-container {
    background: #302f4e;
    flex-basis: 25%;
    border-radius: 20px;
    margin: 0px 15px;
    box-shadow: 0px 10px 50px black;
    flex-grow: 1;
}

.tech-container ul {
    padding: 0px;
    list-style: none;
    text-align: center;
    margin: 15px;
}

.tech-container li {
    font-weight: 300;
    font-size: 24px;
    margin: 10px 0px;
}

.tech-header {
    text-align: center;
    font-weight: 300;
    letter-spacing: 2px;
    font-size: 30px;
    margin: 15px;
}

.tech-icon {
    height: 75px;
    margin: 0 auto;
    display: block;
}

.client-content {
    display: flex;
    margin-top: 40px;
    background: white;
    color: black;
    border-radius: 20px;
    padding: 15px;
    box-shadow: 5px 10px 25px #000000;
    justify-content: center;
    flex-wrap: wrap;
    border: 10px solid #3aaee3;
}

.client-header {
    margin-top: 0px;
    font-weight: 300;
    text-align: center;
    padding-bottom: 5px;
    margin-top: 0px;
    flex-basis: 100%;
    margin-bottom: 25px;
    font-size: 26px;
}

.client-header::after {
    content: '';
    display: block;
    height: 1px;
    width: 40%;
    background: black;
    margin: 0 auto;
    margin-top: 10px;
}

.client {
    flex-basis: 20%;
    text-align: center;
    margin-bottom: 30px;
}

.client-logo {
    height: 45px;
    width: auto;
}

@media only screen and (max-width: 1200px) {
    .client {
        flex-basis: 25%;
    }
    .about-content {
        flex-wrap: wrap;
        justify-content: center;
    }
    .about-text-container {
        margin-top: 25px;
        margin-left: 0px;
        min-width: 0px;
    }
    .about-text {
        text-align: center;
    }
}

@media only screen and (max-width: 767px) {
    .client {
        flex-basis: 30%;
        margin-bottom: 20px;
    }
    .client-logo {
        height: 30px;
    }
    .tech-container {
        flex-basis: 100%;
        margin: 15px 15%;
    }
    .tech-content {
        margin-top: 15px;
        margin-bottom: 0px;
    }
    .client-content {
        margin-top: 15px;
    }
    .about-text {
        font-size: 22px;
    }
    .tech-header {
        font-size: 26px;
    }
    .tech-container li {
        font-size: 22px;
    }
}
.experience-icon {
    height: 100px;
    border-radius: 50%;
}

.experience-header {
    text-align: center;
    font-size: 50px;
}

.experience {
    text-align: center;
}

.experience-date {
    display: block;
    color: #b9b9b9;
    font-size: 22px;
}

.experience-details {
    display: block;
    font-weight: 300;
}

.experience-title {
    font-size: 26px;
    display: block;
    margin-bottom: 10px;
}

.experience-separator {
    height: 75px;
    width: 3px;
    text-align: center;
    background: white;
    margin: 0 auto;
    margin-top: 15px;
    margin-bottom: 15px;
}

.experience-company {
    font-size: 28px;
    font-weight: 400;
    display: block;
    margin-bottom: 10px;
}

@media only screen and (max-width: 767px) {
    .experience-company {
        font-size: 26px;
    }
    .experience-title {
        font-size: 24px;
    }
    .experience-date {
        font-size: 20px;
    }
}
.education-header {
    text-align: center;
    font-size: 50px;
}

.education-content {
    background: #252525;
    border-radius: 20px;
    padding: 45px 20px;
    display: flex;
    box-shadow: 0px 0px 20px #ffc424;
    justify-content: center;
}

.asu-logo {
    height: 175px;
    margin-right: 50px;
}

.education-school {
    font-size: 28px;
    font-weight: 300;
    margin-bottom: 0px;
}

.education-date {
    margin-top: 0px;
    font-weight: 300;
    font-size: 22px;
    color: #b9b9b9;
}

.education-degree {
    font-size: 26px;
    font-weight: 300;
    margin: 10px 0px;
}

.bitmoji-studying {
    border: 2px solid #ffc424;
    height: 150px;
    width: 150px;
    border-radius: 50%;
    box-shadow: 5px 10px 25px #000000;
    margin: 0 auto;
    display: block;
    position: relative;
    bottom: 35px;
}

.education-description {
    text-align: right;
}

@media only screen and (max-width: 767px) {
    .education-content {
        flex-wrap: wrap;
    }
    .education-description {
        text-align: center;
    }
    .asu-logo {
        height: 125px;
    }
}
.projects-header {
    text-align: center;
    font-size: 50px;
}

.projects-list {
    border-radius: 20px;
    box-shadow: 5px 10px 25px #000000;
}

.accordion {
    cursor: pointer;
    padding: 20px 30px;
    width: 100%;
    border: none;
    outline: none;
    font-size: 28px;
    transition: 0.5s;
    text-align: center;
    background: #00c3ff;
    color: white;
    font-weight: 300;
    border-bottom: 1px solid #0a2146;
}

.accordion.active {
    background: #252525;
    border-bottom: none;
}

.accordion.active.last {
    border-radius: 0px;
}

.accordion i {
    float: right;
    font-size: 22px;
}

.accordion i.flipped {
    transform: rotate(180deg);
}

.accordion.first {
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
}

.accordion.last {
    border-bottom-right-radius: 20px;
    border-bottom-left-radius: 20px;
}

.accordion:hover {
    background-color: #252525;
}

.panel {
    padding: 0px 25px;
    background-color: #252525;
    max-height: 0;
    overflow: hidden;
    transition: max-height 500ms ease-out, padding 500ms ease-out;
    font-weight: 200;
    font-size: 22px;
}

.active + .panel {
    padding: 20px 25px;
    padding-top: 0px;
}

.panel img {
    width: 100%;
    border-radius: 10px;
    margin: 25px 0px;
}

.panel h3 {
    font-weight: 500;
}

.panel ul {
    padding: 0px;
    list-style-position: inside;
}
.contact-header {
    font-size: 50px;
    text-align: center;
}

.bitmoji-questions {
    height: 150px;
    width: 150px;
    border: 2px solid white;
    border-radius: 50%;
    display: block;
    margin: 0 auto;
}

.have-questions {
    text-align: center;
    font-size: 28px;
    font-weight: 300;
    text-align: center;
    margin-bottom: 25px;
}

.get-in-touch {
    font-size: 24px;
    font-weight: 200;
    text-align: center;
}

.contact-buttons {
    text-align: center;
}

.email-button, .linkedin-button {
    background: #FF4C60;
    color: white;
    display: block;
    border-radius: 30px;
    font-size: 22px;
    font-weight: 600;
    padding: 15px 35px;
    border: none;
    outline: none;
    cursor: pointer;
    box-shadow: 0px 5px 5px #000000;
    margin: 25px auto;
}

.contact-separator {
    width: 100px;
    height: 1px;
    background: white;
    margin: 0px auto;
    margin-bottom: 25px;
}

.email-button:hover, .linkedin-button:hover {
    -webkit-animation: zoom-out-in 350ms forwards;
            animation: zoom-out-in 350ms forwards;
}

@-webkit-keyframes zoom-out-in {
    35% {
        transform: scale(0.8);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes zoom-out-in {
    35% {
        transform: scale(0.8);
    }
    100% {
        transform: scale(1);
    }
}
